html, body {
  padding: 0;
  margin: 0;
  font: normal 16px 'Open Sans';
  overflow: auto;
  background: #e8f2f9;
  height: 100%;
}

h1, h2 {
  margin-top: 0;
  font-family: 'Open Sans', sans-serif;
  font-size: 22px;
  color: #06683f;
  text-align: center;
}

h2 {
  text-align: left;
}

p {
  margin-bottom: 25px;
}

ul {
  list-style: none;
  list-style-position: inside;
  margin: 0;
  padding: 0;
}

.center {
  text-align: center;
}

img {
  border: 10px solid #defcf0;
  -webkit-box-shadow: 0 0 3px 2px rgba(137, 183, 164,1);
          box-shadow: 0 0 3px 2px rgba(137, 183, 164,1);
  border-radius: 4px;
}

.wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  height: 100%;
  margin: 0 auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  max-width: 860px;
  color: #333;
  -webkit-box-shadow: 0 0 5px rgba(0,0,0,.4);
          box-shadow: 0 0 5px rgba(0,0,0,.4);
  background: #fff;
  border-radius: 4px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

.preview {
  padding: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.preview h1, .preview h2, .preview figure {
  text-align: center;
  margin-bottom: 30px;
}

.preview figure {
  display: -ms-flexbox;
  display: flex;
  margin: 0;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-align: center;
      align-items: center;
}

.preview figure img {
  margin-bottom: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.preview figure figcaption {
  font-size: 12px;
  font-style: italic;
}

.preview p {
  font-size: 16px;
  line-height: 30px;
}

@media only screen and (max-width: 568px) {
  .preview figure {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-align: center;
        align-items: center;
  }

  .preview figure img {
    width: 100%;
  }
}




